import { ButtonHTMLAttributes } from "react";

import { Margins } from "../styleguide";

export interface StyledToggleProps extends Margins {
    isActive?: boolean;
    disabled?: boolean;
}

export interface ToggleProps extends StyledToggleProps, Omit<ButtonHTMLAttributes<HTMLButtonElement>, "onClick"> {
    dataQA: string;
    id?: string;
    className?: string;
    label?: string;
    onClick?: (event: React.MouseEvent<HTMLButtonElement>, newState: boolean) => void;
}

export type ButtonTypes = Pick<ToggleProps, "type">["type"];